export const listOffers = `
  query ListOffers($limit: Int, $nextToken: String) {
    listOffers(limit: $limit, nextToken: $nextToken) {
      items {
        Funding {
          PurchaseType
          LoanType
          DownPayment
        }
        OfferNo
        OfferId
        PropertyData {
          FullAddress
          OwnerNames
        }
        CounterOffer {
          SellerCounter {
            OfferPrice
          }
          BuyerCounter {
            OfferPrice
          }
        }
        Step {
          messages {
            advisor
            buyer
            seller
          },
          id
          name
        }
        CreatedAt
        UpdatedAt
      }
      nextToken
    }
  }
`;
export const listProperties = `
  query ListProperties($limit: Int, $nextToken: String) {
    listProperties(limit: $limit, nextToken: $nextToken) {
      items {
        CreatedAt
        DTPropertyId
        PropertyData {
          City
          County
          FullAddress
          OwnerNames
          State
          StreetAddress
          Zip
        }
        PropertyId
        PropertyNo
      }
      nextToken
    }
  }
`;
export const listForms = `
  query ListForms($limit: Int, $nextToken: String) {
    listForms(limit: $limit, nextToken: $nextToken) {
      items {
        CategoryId
        CreatedAt
        FormId
        FormStatus
        FormTypeId
        Title
        UpdatedAt
      }
      nextToken
    }
  }
`;
export const getOffer = /* GraphQL */ `
	query GetOffer($OfferId: ID!) {
		getOffer(OfferId: $OfferId) {
			Contingencies {
				AllRemoved
				FinanceContingencyDueDate
				FinanceContingencyRemoved
				InspectionContingencyDueDate
				InspectionContingencyRemoved
				SaleOfHomeContingencyDueDate
				SaleOfHomeContingencyRemoved
			}
			Offer {
				ClosingPeriod
				FinanceContingency
				Furniture
				InspectionContingency
				OfferPrice
				SaleOfHomeContingency
			}
			OfferId
			OfferNo
			PropertyData {
				City
				County
				FullAddress
				OwnerNames
				State
				StreetAddress
				VestingType
				YearBuilt
				Zip
			}
			PropertyId
			PropertyNo
			SCAR {
				file {
					path
				}
				id
				key
				name
				presignedUrl
				size
				type
			}
			SignedPurchaseAgreement {
				file {
					path
				}
				id
				key
				name
				presignedUrl
				size
				type
			}
			Step {
				allowed_actions {
					action
					form_action
					next_step_id
				}
				allowed_upload_file_types {
					allow_files_count
					allow_single_file_max_size_mb
					file_type
				}
				buyer_can_submit_offer
				display_on_advisor_portal
				display_on_seller_dashboard
				id
				messages {
					advisor
					buyer
					seller
				}
				name
				waiting_seller_to_act
			}
			StepId
			UpdatedAt
			UpdatedBy
			SubmissionFiles {
				Files {
					presignedUrl
				}
				QuestionName
			}
		}
	}
`;
