import _get from 'lodash.get';
import { FieldPolicy, FetchResult, ApolloCache, gql } from '@apollo/client';
import _isEmpty from 'lodash.isempty';
import { TListProperties, TProperty } from '../../types';
import graphqlSchema from '../../graphql';

export const listProperties: FieldPolicy = {
	keyArgs: false,
	merge<T extends TListProperties>(existing: T | undefined, incoming: T) {
		if (!existing) return incoming;

		return {
			items: [...existing.items, ...incoming.items],
			nextToken: incoming.nextToken,
		};
	},
};

export const updatePropertyCache = (
	cache: ApolloCache<any>,
	result: FetchResult<TProperty>
) => {
	const updateProperty = _get(result, ['data', 'updatePropertyData'], {});
	const { PropertyId: id } = updateProperty;
	cache.writeQuery({
		query: gql(graphqlSchema.getProperty),
		data: {
			getProperty: updateProperty,
		},
		variables: { PropertyId: id },
	});

	cache.modify({
		fields: {
			listProperties(existingProperties: TListProperties) {
				if (_isEmpty(updateProperty)) return existingProperties;

				const { items } = existingProperties;
				const filteredItems = items.filter(
					item => item.PropertyId !== updateProperty.PropertyId
				);
				return { ...existingProperties, items: [...filteredItems, updateProperty] };
			},
		},
	});
};
